import React from 'react';
import {Helmet} from "react-helmet";
import { Link } from "gatsby";
import Footer from '../components/footer';
import MyForm from './myform';
let pathPrefix = `/`
export default () => (

<div>
    <Helmet>
      <meta charSet="utf-8" />
      <title>Convy - Privacy</title>
      <link rel="canonical" href="https://convy.io" />
    </Helmet>
    <div className="row expanded convy_hero_inner">
        <div className="small-3 columns">
            <div className="top-bar-title">
                <a href="http://www.convy.io" onclick="track('Click-logo-top','Click-logo-top')" rel="canonical">
                <img className="convy_logo_app" src={require('../images/convy_logo.svg')} />
                </a>
            </div>
        </div>
        <div className="small-9 columns">
            <div id="topbar-responsive" className="topbar-responsive-links">
                <div className="top-bar-right">
                    <ul className="menu_items">
                        <li>
                        <a href="howconvyworks" onclick="track('Click-logo-top-hdiw','Click-logo-top-hdiw')" rel="canonical">How does it work?</a>
                        </li>
                        <li>
                        <a href="#convy_pricing" onclick="track('Click-logo-top-pricing','Click-logo-top-pricing')" rel="canonical">Pricing</a>
                        </li>
                        <li>
                        <a href="read" onclick="track('Click-logo-top-blog','Click-logo-top-blog')" rel="canonical">Resources</a>
                        </li>
                        <li>
                        <a href="#convy_contact" onclick="track('Click-logo-top-contact','Click-logo-top-contact')" rel="canonical">Contact</a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    {/* begin terms */}
    
    <div className="row align-center padding10_bottom" id="content">
        <div className="small-11 medium-8">
            <h1 className="padding10_top padding5_bottom">Privacy</h1>
          <h4 className="">Last update: Jul 25, 2020.</h4>
          <p>convy.io/uxpages takes the security of its systems and the protection of clients information seriously. convy.io/uxpages (“we”, “us” or “convy.io/uxpages”) is registered to collect and process personal data under the Commission Nationale de l’Informatique et des Libertés (CNIL, France). convy.io/uxpages is the data controller of personal information collected by us. We respect and safeguard the privacy of the clients we are contractually working with, the users of convy.io (“website”), convy.io/uxpages App and readers of our blog which include convy.io/read.</p>
          <p>This Privacy Policy outlines how we will collect and use your personal information (such as your name, your email address or other contact details and any financial payment information), business information (analytics data, testing results) how you can limit our use of this data and how we will safeguard your data. Please read this Privacy Policy carefully which should be read together with our website Terms and conditions.
          </p>
          <h3>1. What information do we gather?</h3>
          <p>1.1 We gather information that relates to, and can identify you (e.g. your name and address) and information that does not identify you (e.g. a visitor’s behaviour patterns when they visit our website).</p>
          <p>We collect information that does not identify you to in order to evaluate our site performance/usability and ensure that our site and services run properly.</p>
          <p>We might also collect IP addresses through the placement of cookies which means your numerical address is automatically collected when you access the website and is placed in our Internet access logs.</p>
          <p>When working with your company and after signing contracts and non-disclosure documentation, we collect performance analytics of the funnels/sections we agreed to optimize. After running testing and for any iteration, we collect analytics data in order to quantify optimizations.</p>
          <h3>2. How do we gather Information</h3>
          <h4>2.1 Information is gathered in 3 ways:</h4>
          <p>(1) indirectly (for example, through our website’s technology or use of an App); and</p>
          <p>(2) directly (for example, when you provide information on various pages of the website or when you return any completed form).</p>
          <p>2.2 We collect information directly from you when you sign up to our services or through the use of cookies. We also collect information when you voluntarily submit it to us.</p>
          <p>2.3 When we collect your information in this way, we will notify you as to how this information will be used and where it will be processed. We will also provide you with the opportunity to indicate your preferences as to the use of your information.</p>
          <p>2.4 The analytics information is collected and stored in our servers. This information is gathered internally with the consultants in charge of the optimization. No information is shared with external third parties.</p>
          <h3>3. Cookies and how we use them</h3>
          <p>3.1 A cookie is a small piece of information sent by a web server to the web browser, which enables the web server to collect information from the browser. Find out more about the use of cookies on http://www.cookiecentral.com.</p>
          <p>3.2 We use the following types of cookie:</p>
          <p>(1a) Personal Analytics cookies that anonymously remember your computer or mobile device when you visit our website. They keep track of browsing patterns and build up a profile of how our readers use the website. We might use that information to target advertisements to you on our and other websites.</p>
          <p>(1b) Users Analytics cookies. convy.io/uxpages uses cookies to run tests and analyze the customer’s website visitor data. The cookies keep track of the variation a visitor has viewed and serve the same variation to the visitor consistently, track goals completed by a visitor, and determine whether a user is part of a test.</p>
          <p>(2) Service cookies that help us to make our website work as efficiently as possible; remember your registration and login details; remember your settings preferences; and meter the number of pages you view for the purpose of administering subscriptions to Magazines and website.</p>
          <p>(3) Third party advertising and analytics cookies, which are placed by or on behalf of independent advertisers who are advertising on our site. These cookies may be placed within the advertisement and elsewhere on our site. They are anonymous – they cannot identify individuals. They are used for statistical analysis by allowing the advertiser to count how many people have seen their advertisement or have seen it more than once. They might also allow the advertiser to tailor advertising to you when you visit other websites.</p>
          <p>3.3 We have no access to third party cookies and third party organisations have no access to ours. Other than allowing the third party cookies to be served we have no part to play in the use of these cookies although we may obtain statistical information from them about advertising on our site. The third party organisations that place cookies have their own strict privacy policies.</p>
          <p>3.4 Most browsers allow you to turn off cookies (see below in section 6). However, switching off cookies may restrict your use of the website and/or delay or affect the way in which the website operates.</p>
          <h3>4. How do you decide how you want to hear from us?</h3>
          <p>4.1 We always explain why and how we might contact you at the point you give your information to us. We also provide the means for you to grant or withhold your permission for us to contact you. We do this by using opt out or opt-in tick boxes. If the boxes aren’t there, it’s because we will not use your information for any other purpose than that for which you give it (e.g. to administer a subscription).</p>
          <p>4.2 You may modify your preferences or get further information by:</p>
          <p>– email to: hello@convy.io</p>
          <p>and your wishes will be honoured.</p>
          <h3>5. How we use your information</h3>
          <p>Any personal information gathered by or supplied to us via the website will be used in accordance with this Privacy Policy, and in accordance with your wishes as indicated to us and in accordance with the Act.</p>
          <p>5.1 We might use your personnal information to:</p>
          <p>(1) remember you when you visit our website and track your browsing patterns by using cookies. You can prevent this; section 6 deals with this in more detail;</p>
          <p>(2) place your anonymised information into audience segments to identify your interests so that we might serve you with offers that will interest you;</p>
          <p>(3) ensure any products or services you receive are correctly administered;</p>
          <p>(4) contact you by email, mail, telephone or sms text message to let you know about any of our products, services or promotions (which, in some cases may be provided by third parties) but only according to the permissions you give us;</p>
          <p>(5) ensure materials on the website or in apps are presented in the most effective manner for you and your computer or mobile device;</p>
          <p>(6) collate and log numeric internet addresses to improve the website and to monitor website usage;</p>
          <p>(7) assess and understand customer feedback and identify usage hotspots.</p>
          <p>5.1 We might use your business information to:</p>
          <p>(1) run optimization test;</p>
          <p>(2) make comparison table based on the analytics provided for a specific funnel or part of a website/app;</p>
          <p>(3) run any consulting activity according to the permissions you give us;</p>
          <p>5.3 If you have subscribed to any of our services, we will use the information you provide to keep you updated on those services unless you have told us otherwise.</p>
          <h3>6. Managing Cookie Preferences</h3>
          <p>6.1 Most browsers allow you to turn off cookies. To do this look at the “help” menu on your browser. Switching off cookies may restrict your use of the website and/or delay or affect the way in which it operates.</p>
          <p>6.2 If you would prefer to just restrict third party cookies, visit www.youronlinechoices.com or click on the AdChoices logo that you will see in the corner of advertisements that carry behavioural targeting cookies.</p>
          <h3>7. Security</h3>
          <p>7.1 The password you provide when registering with the website is encrypted to ensure protection against unauthorised access of your personal information.</p>
          <p>7.2 We invest in high-quality security and do our utmost to protect user privacy. No data transmission over the Internet can be entirely secure, and therefore we cannot guarantee the security of your personal and business information and/or use of the website. Any information that you send is at your own risk and may be read by others. However once we have received your personal and business information we use strict procedures both ourselves and with our suppliers to protect the security of your personal and business information. </p>
          <p>7.3 We (convy.io/uxpages) commit to perform research only within the scope set out and contractually agreed with our clients. convy.io/uxpages makes every effort to avoid privacy violations, degradation of user experience, disruption to production systems, and destruction of data during testing</p>
          <p>7.4 We save data in a server provided by OVH and located in Paris, France. Server details are : FS-MAX, Intel  2x Xeon E5-2620v3, 12c/24t - 2.4GHz /3.2GHz, 64GB DDR4 ECC 1866 MHz - Up to 36 disks, 500 Mbps bandwidth vRack: 40 Gbps. Not a big performance beast but a safe device in a safe environment. We backup the information in OVH Data Center spread across different locations. We do not store any customer data locally.
            The variation configurations required to run the tests for visitors can also be self-hosted on the customer’s servers for added security control. Physical access to the OVH data centers is restricted by OVH. No physical access is allowed to any of the employees of convy.io/uxpages.</p>
          <p>7.5 Master &amp; Optimization codes are stored in in OVH Data Center spread across different locations.  convy.io/uxpages employs strict role-based security / passwords for access to the code. If your servers are used to run tests, commits to production code are strictly reviewed and approval is restricted to client validation, after passing Unit Testing and QA in Test and Staging. When running tests using clients structure, commits to production code are under client policy and process.</p>
          <h3>8. Disclosing your information</h3>
          <p>8.1 We may disclose your personal information if required to do i) in order to fulfil any request you have made through us , ii) by law or iii) in good-faith believing such action is necessary to comply with the law or contract e.g. for a bank or card company to process your payment for purchase of goods.</p>
          <p>8.2 We will not sell or otherwise share your personal information with third parties without your consent except in the case of our data service providers (who are used for example to manage your subscription).</p>
          <p> 8.3 The consultants working for convy.io/uxpages agree and sign a confidentiality agreement (“Confidential Information Agreement”). The consultants or any individual involved in a project in convy.io/uxpages shall not use, disclose, publish, or distribute any such Confidential Information or the vulnerability details, including without limitation any information regarding the optimization process / results / business information for your company.</p>
          <h3>9. Accessing and Updating your personal information</h3>
          <p>We want to ensure that we provide you with the best possible service. Please therefore always keep us updated of any changes to your personal information, for example, if your email address changes.</p>
          <p>9.1 How do I change my personal details?</p>
          <p>You can update your details and marketing preferences by accessing your account pages on the website by emailing contact@mekacreative.io</p>
          <p>9.2 What information do you hold on me?</p>
          <p>You have the right to access the personal information we hold about you. To obtain a copy of the personal information we hold about you, please send an email to hello@convy.io</p>
          <p>9.3 What information do you hold on my company?</p>
          <p>You have the right to access the business information we hold about your company (analytics, testing/optimization details &amp; results). To obtain a copy of the business information we hold about your company, please send an email to hello@convy.io</p>
          <p>There will be an administrative charge of €10 for providing a copy of this information.</p>
          <h3>10. What does providing us with business &amp; personal information mean?</h3>
          <p>By giving us personal information about you and business information about your company, you agree that your personal information and business information can be used as set out in this Privacy Policy. If we change the Privacy Policy, we will post the changes on this page. Please check the website regularly for any changes to this Privacy Policy.</p>
          <h3>11. Associates Program / third parties</h3>
          <p>convy.io/uxpages uses Google Analytics Solutions / Optimize, an online optimization and testing tool. When testing pages and analytics data are linked, the information will also be gathered with Google. Please refer to https://www.google.com/intl/en/policies/privacy/ and https://support.google.com/360suite/optimize#topic=6314903 for more details about Google data privacy.</p>
          <h3>12. If you don’t feel we’re adhering to this Privacy Policy, what should you do?</h3>
          <p>If at any time you believe that we have not adhered to the terms of this Privacy Policy, please notify us by email to hello@convy.io. and we will make any efforts we can to determine and solve the problem promptly.</p>
          <h3>13. Support</h3>
          <p>For any questions regarding these Terms Of Service, feel free to contact us by email to hello@convy.io.</p>
          {/*End terms*/}
        </div>{/*column*/}
      </div>{/*row*/}
    <div className="convy_footer">
        <Footer/>
    </div>
</div>
) 

